.page__main {
    font-family: $bodyFont;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    h1, h2, h3 {
        font-family: $titleFont;
    }

    .section__list--movies {
        width: 80vw;
        flex-grow: 2;
        order: 1;
        text-align: center;

        @include forMobile {
            width: 70vw;
            }

        .search__shows {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding: 10px;
            border-bottom: 1px solid #333333;
        }

        .search__movie {
            width: 300px;
            border: 1px solid gray;
            margin: 15px;
            font-size: 16px;

            @include forMobile {
                width: 150px;
                }
        }

        .btn__search {
            width: 300px;
            background-color: rgba(255, 255, 255, 0.80);
            border-radius: 5px;
            border: none;
            font-size: 18px;
            
                
            &:hover {
                background-color: rgba(222, 145, 172, 0.80);
                color: white;
            }

            &:focus {
                background-color: rgba(222, 145, 172, 0.80);
                color: white;
            }
        }

        .show__shows {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;
            overflow: scroll;

            .show__list--item {
                width: 250px;
                height: 400px;
                padding: 10px;
                margin: 10px 40px;
                display: flex;
                flex-direction: column;
                box-shadow: 1px 1px 10px 0px rgba(173, 173, 173, 0.8);
                cursor: pointer;
                position: relative;

                &:hover {
                    // box-shadow: 1px 1px 10px 0px rgb(222, 145, 172);
                    
                }
                
                img {
                    width: 210px;
                    height: 295px;
                    margin: auto;
                }

                .show__item--info {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    background-color: rgb(222,144,169);
                    color: white;
                    overflow: hidden;
                    width: 100%;
                    height: 10%;
                    transition: 0.5s ease;
                    display: flex;
                    flex-direction: column;
                    font-size: 12px;
                    margin-top: 10px;
                    padding: 0 10px 0px 10px;
                    font-weight: 400;
                    font-family: 'Playfair Display', serif;
                    line-height: 1.2;

                    .overlay__text {
                        font-size: 16px;
                        font-weight: bold;
                        margin: 4.5% 0;
                    }

                    &:hover, .overlay {
                        height: 100%;
                    }
                }
            }
                    .fav__show--style {
                        width: 250px;
                        height: 400px;
                        padding: 10px;
                        margin: 10px 40px;
                        display: flex;
                        flex-direction: column;
                        // background-color: rgb(210, 158, 248);
                        background: url(https://images.unsplash.com/photo-1530367215503-327c484541ee?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1766&q=80);
                        background-position: center;
                        cursor: pointer;
                        position: relative;

                        img {
                            width: 210px;
                            height: 295px;
                            margin: auto;
                        }

                        .show__item--info {
                            position: absolute;
                            bottom: 0;
                            left: 0;
                            right: 0;
                            background-color: rgb(216, 216, 216);
                            overflow: hidden;
                            width: 100%;
                            height: 10%;
                            transition: 0.5s ease;
                            display: flex;
                            flex-direction: column;
                            font-size: 12px;
                            margin-top: 10px;
                            font-weight: 400;
        
                            .overlay__text {
                                font-size: 18px;
                                margin: 5% 0;
                            }
        
                            &:hover, .overlay {
                                height: 100%;
                            }
                        }
                    }
        }
    }

    aside {
        text-align: center;
        box-shadow: 2px 2px 10px gray;
        height: 100vh;
        margin-top: 0;
        
        
        .fav__movies--title {
            padding: 20px 0;
        }
        
        .btn__remove--all {
            background-color: rgba(255, 255, 255, 0.80);
            border-radius: 5px;
            border: 1px solid lightgray;
            font-size: 12px;

            &:hover {
                background-color: rgba(222, 145, 172, 0.80);
                color: white;
            }

            &:focus {
                background-color: rgba(222, 145, 172, 0.80);
                color: white;
            }
        }
    
    .section__fav--movies {
        width: 20vw;
        height: 90vh;
        padding: 20px;
        order: 0;
        text-align: center;
        overflow: scroll;
        display: flex;
        flex-wrap: nowrap;
        flex-direction: column;
        align-items: start;

        @include forMobile {
            width: 30vw;
            align-items: center;
        }

        li {
            display: flex;
            flex-direction: column;
            align-items: center;
            position: relative;
                
            .btn__remove--single {
                position: absolute;
                top: 0;
                background-color: black;
                color: white;
                border-radius: 100%;
                border: 2px solid rgba(255, 255, 255, 0.85);

                &:hover {
                 background-color: rgb(222, 145, 172);   
                }
            }
            @include forDesktop {
              flex-direction: row;
            }

            img {
                border: 1px solid #ddd;
                border-radius: 4px;
                padding: 5px;
                width: 150px;
                

              @include forMobile {
                width: 50px;
                
              }
            }
        }
    }
}
}

.hidden {
    // position: absolute;
    // left: -20vw;
    display: none;
}
.full_screen {
    height: 100vh;
    font-size: 4vh; 
    // font-weight: bold;  
    background: url(https://images.unsplash.com/photo-1565098772267-60af42b81ef2?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=3484&q=80) no-repeat;
    background-size: cover;
    background-position: center;
    color: white;
}
.medium_screen {
    height: 90vh;
}

